<template>
  <div class="searchIntroducer">
    <div class="search-container">
      <mt-field placeholder="请输入介绍人姓名" v-model="introducer" class="search-input"></mt-field>
      <span class="close-btn" @click="closeSearch('close')">取消</span>
    </div>
    <div class="search-list">
      <div class="item" v-for="(item,index) in result" @click="closeSearch('needData',index)">
        <span>{{item.name}}</span>
        <span>{{item.idno}}</span>
        <span>{{item.serviceName}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  import {getInstroduce} from  "@/api/bindInfo"
  import {Toast} from 'mint-ui';
  export default {
    name: "searchIntroducer",
    data() {
      return {
        introducer: '',
        result: []
      };
    },
    components: {

    },
    watch:{
      introducer(newName, oldName){
       if(/^[\u4e00-\u9Fa5]{2,8}$/.test(newName)){
         this.getInfo(newName);
       }
      }
    },
    methods: {
      closeSearch(gotoType,selectedIndex) {
        if (gotoType === 'needData') {
          sessionStorage.setItem('introducerInfo', JSON.stringify({
            name: this.result[selectedIndex].name,
            cardID: this.result[selectedIndex].idno,
            introduceId: this.result[selectedIndex].rcordId,
            belongTo: this.result[selectedIndex].serviceName
          }));
        }

        this.$router.go(-1);
      },
      getInfo(name){
        let _this= this;
        getInstroduce(name).then((res)=>{
            _this.result = res.data;
            console.log(JSON.parse(res.data))
            if(res.data.length === 0){
              Toast({
                message: '没有该用户！',
                duration: 1000
              });
            }
        }).catch((err)=>{
         console.log(err)
        })
      }
    }
  };
</script>

<style lang="scss">
  .searchIntroducer {
    width: 100%;
    min-height: 100vh;
    font-size: .16rem;
    margin-bottom: -0.47rem;

    .search-container {
      display: flex;
      align-items: center;
      padding-right: .12rem;

      .mint-cell {
        border-bottom: 0;
      }

      .search-input {
        width: 90%;
        height: .28rem;

        .mint-cell-value {
          position: relative;

          .mint-field-core {
            padding: .04rem .2rem .04rem .43rem;
            font-size: .14rem;
            background:rgba(245,245,245,1);
            border-radius: .06rem;
          }

          .mint-field-clear {
            position: absolute;
            right: 5px;
          }
        }
      }

      .close-btn {
        width: 10%;
        text-align: right;
        color: #108EE9;
      }
    }

    .search-list {
      padding-left: .12rem;
      .item {
        height: .6rem;
        line-height: .6rem;
        border-bottom: 1px solid #ddd;

        span {
          padding-left: .06rem;
        }
      }
    }
  }
</style>



